@charset "utf-8";

/*#fullpage*/
#fullpage{width: 100%;}
#fullpage .section{width: 100%; position: relative;}
#fullpage .section .result_board{position: absolute; top: 30px; right: 180px; height: calc(100% - 60px); z-index: 1;}

/*#fullpage .background*/
#fullpage .section .background{width: 100%; height: 100%; position: absolute; top: 0; left: 0;}
#fullpage #home .background{background: url(./img/background_home.jpeg) no-repeat center; background-size: cover;}
#fullpage #about .background{background: url(./img/background_about.jpeg) no-repeat center; background-size: cover;}
#fullpage #project .background{background: url(./img/background_project.jpeg) no-repeat center; background-size: cover;}

/*.main #fullpage pagination*/
#fp-nav.fp-right{right: -85px !important; transition: .5s !important; transition-timing-function: ease !important;}
#fp-nav ul li, .fp-slidesNav ul li{margin: 37px !important; width: 200px !important; height: 20px !important;}
#fp-nav ul li .fp-tooltip.fp-right{overflow: visible !important; opacity: 1 !important; left: 0 !important; width: 0 !important; font-size: 1.7rem !important; transition: 1s !important; color: #000 !important;}
#fp-nav ul li a span, .fp-slidesNav ul li a span, #fp-nav ul li a.active > span{width: 5px !important; height: 5px !important; background: #000 !important; transition: 1s !important; border-radius: 50% !important;}
#fp-nav ul li:hover a.active span, #fp-nav ul li a.active span, .fp-slidesNav ul li:hover a.active span, .fp-slidesNav ul li a.active span, #fp-nav ul li:hover a span, #fp-nav ul li a span, .fp-slidesNav ul li:hover a span, .fp-slidesNav ul li a span {height: 5px !important; width: 5px !important;  margin: -6px 0 0 -6px !important; border-radius: 50% !important;}
#fp-nav > ul > li > div{transition: .5s !important;}   