@keyframes type{
    from{box-shadow: inset -3px 0px 0px #fff;}
    to{box-shadow: inset -3px 0px 0px transparent;}
}

.not_found_wrap{background: #343a40; transition: all 0.6s; height: 100%; font-family: 'Lato', sans-serif; color: #fff; margin: 0; display: table; width: 100%; height: 100vh; text-align: center;}
.fof{display: table-cell; vertical-align: middle;}
.fof_h1{font-size: 50px; display: inline-block; padding-right: 12px; animation: type .5s alternate infinite;}

